app.ports.scrollToId.subscribe((id) => {
    // console.log('scrollToId', id)
    const item = document.querySelector('#' + id)
    if (!item) return
    const scroller = document.querySelector('#program-explorer')
    if (!scroller) return
    scroller.scrollTop =
        scroller.scrollTop +
        (item.getBoundingClientRect().y -
            scroller.getBoundingClientRect().height / 2)
})

type AudioControls = {
    play(): void
    pause(): void
}

// https://stackoverflow.com/questions/6343450/generating-sound-on-the-fly-with-javascript-html5
function makeAudio(): AudioControls {
    const context = new window.AudioContext()
    const osc = context.createOscillator() // instantiate an oscillator
    const vol = context.createGain()
    osc.type = 'square'
    osc.frequency.value = 80 // Hz
    // osc.connect(context.destination) // connect it to the destination
    osc.connect(vol)

    vol.gain.value = 0.1 // from 0 to 1, 1 full volume, 0 is muted
    // vol.gain.value = 1.0 // from 0 to 1, 1 full volume, 0 is muted
    vol.connect(context.destination)
    // // osc.stop(context.currentTime + 0.2) // stop 2 seconds after the current time
    let playing = false
    return {
        play() {
            if (!playing) {
                osc.start()
                playing = true
            }
            vol.gain.value = 0.1
        },
        pause() {
            // console.log(`audio#pause`, playing)
            if (playing) {
                console.log('muting...')
                vol.gain.value = 0.0
                // console.log('osc.stop()')
                // osc.stop()
                // playing = false
            }
        },
    }
}

