let audio: AudioControls
app.ports.playAudio.subscribe((shouldPlay) => {
    if (!audio) {
        audio = makeAudio()
        console.log('created audio')
    }
    if (shouldPlay) {
        audio.play()
    } else {
        audio.pause()
    }
})
